<template>
  <div id="login">
    <div class="login-box">
      <img class="login-logo" src="../../assets/logo.png" alt="Longdiera.com">
      <Card class="login-card">
        <p slot="title">
          LongDiEra.com Admin
        </p>
        <p>
          <i-form ref="formItem" :model="formItem" :rules="ruleLogin">
            <Form-item prop="username">
              <i-input type="text" v-model="formItem.username" placeholder="username...">
                <Icon type="ios-person-outline" slot="prepend"></Icon>
              </i-input>
            </Form-item>
            <Form-item prop="password">
              <i-input type="password" v-model="formItem.password" placeholder="password...">
                <Icon type="ios-locked-outline" slot="prepend"></Icon>
              </i-input>
            </Form-item>
            <Form-item>
              <i-button class="pull-right" type="ghost" @click.native="handleSubmit('formItem')"> Login </i-button>
            </Form-item>
          </i-form>
        </p>
      </Card>
      <span class="copy-box">Design By <span class="text-orange">Snowdiva</span></span>
    </div>
  </div>
</template>

<script>
  import md5 from 'md5/md5.js'

  export default {
    data () {
      return {
        formItem: {
          username: '',
          password: ''
        },
        ruleLogin: {
          username: [
            { required: true, message: '请填写用户名', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请填写登录密码', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let postData = this.formItem
            postData.password = md5(this.formItem.password)
            this.$http.post('/admin_api/login', postData).then(response => {
              if (response.body.error === 0) {
                sessionStorage.setItem('access_token', response.body.data.access_token)
                sessionStorage.setItem('user', JSON.stringify(response.body.data.user))
                this.$Message.success('欢迎,' + response.body.data.user.username)
                this.$router.push('/home')
              } else {
                this.$Message.error(response.body.message)
              }
            }, response => {
              console.log(response)
              this.$Notice.error({'title': '有错误', 'desc': '传输状态错误'})
            })
          } else {
            this.$Message.error('填写不正确')
          }
        })
      }
    }
  }
</script>

<style>
  #login {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    background-color: #ffffff;
  }
  .login-box {
    width: 320px;
    height: auto;
    margin: 100px auto 0;
  }
  .login-box .login-logo {
    width: 300px;
    height: 174px;
    margin: 0 10px;
  }
  .login-box .login-card {
    background-color: #f5f7f9;
  }
  .login-box .login-card .pull-right {
    float: right;
  }
  .login-box .copy-box {
    display: block;
    width: 100%;
    height: auto;
    text-align: center;
    margin: 6px 0;
  }
  .login-box .copy-box .text-orange {
    font-size: 14px;
    color: #ff9900;
  }
</style>



